
import { defineComponent, ref } from "vue";
import { getCSSVariableValue } from "@/assets/ts/_utils";
import axios from "axios";
interface MonthValue {
  id: number;
  month: string;
  value: number;
  year: string;
  chart_data_id: number;
  created_at: any;
  updated_at: any;
}
export default defineComponent({
  computed: {
    currentRouteName() {
      return window.location.origin;
    },
  },
  name: "widget-1",
  props: {
    year: null,
    chart: null,
    barColor: String,
    widgetClasses: String,
    height: Number,
  },
  data() {
    return {
      dialog: false,
      monthValue: {
        year: null,
      },
      months: [
        { month: "jan", value: 0 },
        { month: "Fév", value: 0 },
        { month: "Mar", value: 0 },
        { month: "Avr", value: 0 },
        { month: "Mai", value: 0 },
        { month: "Juin", value: 0 },
        { month: "Juil", value: 0 },
        { month: "Août", value: 0 },
        { month: "Sept", value: 0 },
        { month: "Oct", value: 0 },
        { month: "Nov", value: 0 },
        { month: "Déc", value: 0 },
      ],
    };
  },
  watch: {
    "monthValue.year"(val) {
      const filteredMonthValues = this.chart.month_values.filter(
        (e) => e.year == val
      );
      if (filteredMonthValues.length > 0) {
        this.months = filteredMonthValues;
      }
    },
  },
  components: {},
  setup(props) {
    const labelColor = getCSSVariableValue("--bs-gray-500");
    const borderColor = getCSSVariableValue("--bs-gray-200");
    const secondaryColor = getCSSVariableValue("--bs-gray-300");
    let months: string[] = [];
    let values: number[] = [];
    let cumul = 0;
    props.chart.month_values.forEach((e: MonthValue) => {
      if (e.year === props.year) {
        cumul += e.value;
        months.push(e.month);
        values.push(e.value);
      }
    });
    console.log(values);
    const series = [
      {
        name: props.chart.chartName,
        data: values,
      },
      {
        name: "Cumul",
        data: Array(values.length).fill((cumul / values.length).toFixed(2)),
      },
    ];
    const options = {
      chart: {
        fontFamily: "inherit",
        type: "bar",
        toolbar: {
          show: false,
        },
      },
      plotOptions: {
        bar: {
          horizontal: false,
          columnWidth: ["30%"],
          endingShape: "rounded",
        },
      },
      legend: {
        show: false,
      },
      dataLabels: {
        enabled: false,
      },
      stroke: {
        show: true,
        width: 2,
        colors: ["transparent"],
      },
      xaxis: {
        categories: months,
        axisBorder: {
          show: false,
        },
        axisTicks: {
          show: false,
        },
        labels: {
          style: {
            colors: labelColor,
            fontSize: "12px",
          },
        },
      },
      yaxis: {
        labels: {
          style: {
            colors: labelColor,
            fontSize: "12px",
          },
        },
      },
      fill: {
        opacity: 1,
      },
      states: {
        normal: {
          filter: {
            type: "none",
            value: 0,
          },
        },
        hover: {
          filter: {
            type: "none",
            value: 0,
          },
        },
        active: {
          allowMultipleDataPointsSelection: false,
          filter: {
            type: "none",
            value: 0,
          },
        },
      },
      tooltip: {
        style: {
          fontSize: "12px",
        },
        y: {
          formatter: function (val) {
            // return "$" + val + " thousands";
            return val;
          },
        },
      },
      colors: [props.barColor, secondaryColor],
      grid: {
        borderColor: borderColor,
        strokeDashArray: 4,
        yaxis: {
          lines: {
            show: true,
          },
        },
      },
    };
    return {
      options,
      series,
    };
  },
  methods: {
    addMonthValues(id) {
      const monthValues = this.months.map((month) => {
        return {
          month: month.month,
          value: month.value,
          year: this.monthValue.year,
        };
      });
      console.log(monthValues);
      axios
        .post(
          this.currentRouteName + `/api/chart-data/${id}/month-values`,
          monthValues
        )
        .then((response) => {
          console.log(response.data);
          this.dialog = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
});
